*{
  font-family: Arial, Helvetica, sans-serif;
}


body{
  padding: 60px;
}

input{
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card-grid{
display: flex;
flex-direction: row;
flex-wrap: wrap;

}
.card{
 align-content: center;
  border: 1px solid grey;
  overflow: hidden;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  text-align: center;
  padding: 5px;
}

.card img{
  width: 100%;
  max-height: 170px;

}